<template>
  <div>
    <el-table :data="versionData" style="width: 100%; display:inline-block; margin-top:36px">
      <el-table-column prop="version_code" align="center" label="版本号" width="100"></el-table-column>
      <el-table-column prop="version_date" align="center" label="版本更新时间" width="240"></el-table-column>
      <el-table-column prop="version_content" align="center" label="版本更新内容"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      versionData: [{
        version_code: 'v1.0.0',
        version_date: '2019-8-8',
        version_content: '第一版'
      }]
    }
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['系统设置', '版本管理'])
  },
  mounted () {
  },
  methods: {
  }
}
</script>

<style scoped>
.el-table >>> th {
  background-color: #e3eefa;
  text-align: center;
}
.el-table >>> th  .cell {
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  color: #000000;
  font-size: 16px;
  font-weight: bold
}
.el-table >>> th:first-child .cell {
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bold;
  color: #000000;
}
.el-tabs--card >>> .el-tabs__header .el-tabs__item.is-active {
  border-bottom: #75ace6 5px solid;
  color: #000000;
  font-weight: bolder;
}
</style>



